import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import User from './views/nav1/user.vue'
import Customer from './views/nav1/customer.vue'
import Department from './views/nav1/department.vue'
import UserDevice from './views/nav1/userDevice.vue'
import list from './views/nav2/list.vue'
import task from './views/nav3/task.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
	{
	    path: '/',
	    component: Home,
	    name: '任务统计',
	    iconCls: 'fa fa-bar-chart',
	    children: [
	        { path: '/echarts', component: echarts, name: '任务统计' }
	    ]
	},
    {
        path: '/',
        component: Home,
        name: '用户管理',
        iconCls: 'fa fa-address-card',//图标样式class
        children: [
            { path: '/main', component: Main, name: '主页', hidden: true },
            { path: '/user', component: User, name: '用户管理' },
			{ path: '/customer', component: Customer, name: '客户管理' },
			{ path: '/department', component: Department, name: '部门管理' },
			{ path: '/userDevice', component: UserDevice, name: '查看设备',hidden: true}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '设备管理',
        iconCls: 'fa fa-wrench',
        children: [
			{ path: '/list', component: list, name: '设备' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '任务管理',
        iconCls: 'el-icon-message',
        // leaf: true,       //只有一个节点
        children: [
            { path: '/task', component: task, name: '页面3' }
        ]
    },
    
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


