<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<!-- <el-form-item>
					<el-input v-model="filters.name" placeholder="姓名"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getUsers">查询</el-button>
				</el-form-item> -->
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column label="序号" type="index" width="100">
			</el-table-column>
			<el-table-column prop="userName" label="用户名" width="140" sortable>
			</el-table-column>
			<el-table-column prop="typeName" label="用户类型" width="140"  sortable>
			</el-table-column>
			<el-table-column prop="mobile" label="手机号" width="150" sortable>
			</el-table-column>
			<el-table-column prop="createdTime" label="创建时间" width="160" sortable>
			</el-table-column>
			<!-- <el-table-column prop="addr" label="地址" min-width="180" sortable>
			</el-table-column> -->
			<el-table-column label="操作" width="300">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<!-- <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button> -->
					<el-button size="small" @click="addSeeBtn(scope.$index, scope.row)">查看设备</el-button>
					<!-- <el-button size="small" @click="addDeviceBtn(scope.$index, scope.row)">添加设备</el-button> -->
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button> -->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="用户名" prop="userName ">
					<el-input v-model="editForm.userName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="用户类型">
					<el-radio-group v-model="editForm.userType" disabled>
						<el-radio class="radio" :label="0">客户</el-radio>
						<!-- <el-radio class="radio" :label="1">服务人员</el-radio>
						<el-radio class="radio" :label="2">审核人员</el-radio> -->
					</el-radio-group>
				</el-form-item>
				<!-- <el-form-item label="部门">
					<el-input v-model="editForm.age" ></el-input>
				</el-form-item>
				<el-form-item label="用户类型">
					<el-input  v-model="editForm.uestype"></el-input>
				</el-form-item> -->
				<el-form-item label="手机号">
					<el-input type="mobile " v-model="editForm.mobile " disabled></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="用户名" prop="userName">
					<el-input v-model="addForm.userName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="用户类型">
					<el-radio-group v-model="addForm.userType">
						<el-radio class="radio" :label="0">客户</el-radio>
						<!-- <el-radio class="radio" :label="1">服务人员</el-radio>
						<el-radio class="radio" :label="2">审核人员</el-radio> -->
					</el-radio-group>
				</el-form-item>
				<el-form-item label="手机号">
					<el-input v-model="addForm.mobile" ></el-input>
				</el-form-item>

				<!-- <el-form-item label="地址">
					<el-input type="textarea" v-model="addForm.addr"></el-input>
				</el-form-item> -->
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	
		
		<!-- 添加设备 -->
		<el-dialog title="添加设备" v-model="addFormDevice" :close-on-click-modal="false">
			<el-form :model="addDevice" label-width="80px" :rules="addDeviceRules" ref="addDevice">
				<el-form-item label="设备名" prop="name">
					<el-input v-model="addDevice.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="设备类型">
					<el-radio-group v-model="addDevice.type">
						<el-radio class="radio" :label="0">设备</el-radio>
						<!-- <el-radio class="radio" :label="1">服务人员</el-radio>
						<el-radio class="radio" :label="2">审核人员</el-radio> -->
					</el-radio-group>
				</el-form-item>
				<el-form-item label="型号">
					<el-input v-model="addDevice.moudl" ></el-input>
				</el-form-item>
		
				<!-- <el-form-item label="地址">
					<el-input type="textarea" v-model="addForm.addr"></el-input>
				</el-form-item> -->
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormDevice = false">取消</el-button>
				<el-button type="primary" @click.native="addDevSubmit" :loading="addDevLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getCusList, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
					id: 0,
					userName: '',
					userType: -1,
					mobile: ''
				},
				//新增界面是否显示
				addFormVisible: false,
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
					
				},
				//新增界面数据
				addForm: {
					userName: '',
					userType: -1,
					mobile: ''
					
				},
				//查看设备界面是否显示
				addFormSee:false,  
				addSeeLoading:false,
				addSeeRules:{
					
				},
				//查看设备数据
				addSee: {
					name: '',
					type: -1,
					moudl: ''
					
				},
				//添加设备界面是否显示
				addFormDevice:false,  
				addDevLoading:false,
				addDeviceRules:{
					
				},
				//添加设备数据
				addDevice: {
					name: '',
					type: -1,
					moudl: ''
					
				},

			}
		},
		methods: {
		
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
					pageNum: this.page,
					pageSize :10
				};
				this.loading = true;
				//NProgress.start();
				getCusList(para).then((res) => {
					this.total= res.data.data.total;
					this.users = res.data.data.dataList;
					if(this.users.length>0){
						for(let i=0;i<this.users.length;i++){
							this.users[i].typeName="客户";
						
						}
					}
					this.loading = false;
					//NProgress.done();
				});
				
			},
			//删除
			// handleDel: function (index, row) {
			// 	this.$confirm('确认删除该记录吗?', '提示', {
			// 		type: 'warning'
			// 	}).then(() => {
			// 		this.listLoading = true;
			// 		//NProgress.start();
			// 		let para = { id: row.uuid };
			// 		removeUser(para).then((res) => {
						
			// 			this.listLoading = false;
			// 			//NProgress.done();
			// 			this.$message({
			// 				message: '删除成功',
			// 				type: 'success'
			// 			});
			// 			this.getUsers();
			// 		});
			// 		this.listLoading = false;
			// 	}).catch(() => {

			// 	});
			// },
			//显示编辑界面
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					userName: '',
					userType: -1,
					mobile: ''
				};
			},
			//查看设备跳转
			addSeeBtn:function (index,row) {
				console.log(row);
				let uuid=row.uuid;
				console.log(uuid);
				this.$router.push({path:'./userDevice',query:{id:uuid}});
			},
		
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
						
							//para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							let user = Object.assign({}, this.addForm);
							user.deptId="10";
							user.password="123456";
							//para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(user).then((res) => {
								this.addLoading = false;
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
		

			
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					for(let i=0;i<ids.length;i++){
						for(let j=0;j<this.users.length;j++){
							if(ids[i]==this.users[j].id){
								this.users.splice(j,1);
							}
						};
					}
					this.$message({
						message: '删除成功',
						type: 'success'
					});
					this.listLoading = false;
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>

</style>


