import axios from 'axios';

let base = 'http://127.0.0.1:8091/xizhu';

axios.interceptors.request.use(
    config => {
      if (sessionStorage['token']) {
        config.headers.common['Authorization'] = `${sessionStorage['token']}`;
      }
      return config;
    },
    err => {
      return Promise.reject(err);
    }
  )

export const requestLogin = params => { return axios.post(base+`/login/admin`, params).then(res => res.data); };

export const getUserList = params => { return axios.post(`${base}/user/list`, { params: params }); };//用户分页

export const addUser = params => { return axios.post(`${base}/user`, params).then(res => res.data); };//新增用户

export const editUser = params => { return axios.put(`${base}/user`, params).then(res => res.data); };//修改用户

export const removeUser = params => { return axios.delete(`${base}/user/`+params.id, params).then(res => res.data); };//删除用户

export const getUserDetil = params => { return axios.get(`${base}/user/`+params.userId, { params: params }); };//获取指定用户信息

export const getDeptList = params => { return axios.get(`${base}/dept/list/deptList`, { params: params }); };//部门列表

export const addDepart = params => { return axios.post(`${base}/dept`, params).then(res => res.data); };//部门分页

export const editDept = params => { return axios.put(`${base}/dept`, params).then(res => res.data); };//部门编辑

export const removeDept = params => { return axios.delete(`${base}/dept/`+params.deptId, params).then(res => res.data); };//部门删除

export const NewAddDevice = params => { return axios.post(`${base}/device`, params).then(res => res.data); };//新增用户设备

export const getDevList = params => { return axios.get(`${base}/customer/`+params.userId+`/device`, { params: params }); };//获取客户设备列表
// export const getDevice= params => { return axios.get(`${base}/xizhu/device/`, { params: params }); };//获取设备详情
export const editDevice = params => { return axios.put(`${base}/device`, params).then(res => res.data); };//部门编辑

export const getCusList = params => { return axios.post(`${base}/user/customer/list`, { params: params }); };//客户分页



